.Programs{
    display: flex;
    flex-direction: column;
    gap: 2rem;
    padding: 0.2rem;
}

.explain{
    font-size: 1.5rem;
    font-weight: 200;
    text-transform: none;
    letter-spacing: 1px;
    display: flex;
    justify-content: start;
    color: rgb(255, 255, 255);
    margin-bottom: 2rem;
}

.programs-header{
    display: flex;
    gap: 5rem;
    font-weight: bold;
    font-size: 3rem;
    justify-content: center;
    color: white;
    text-transform: uppercase;
    font-style: italic;
}

.programs-categories{
    display: flex;
    gap: 3rem;
    justify-content: center;
}

.category{
    display: flex;
    height: 20rem;
    width: 25rem;
    flex-direction: column;
    background-color: rgba(98, 101, 134, 0.488);
    padding: 2rem;
    gap: 1rem;
    color: white;
    justify-content:space-between ;
    border-radius: 15px;
}

.category>:nth-child(1)
{
    width: fit-content;
    height: 2rem;
    fill: white;
    font-weight: bold;
    font-size: 1.2rem;
}


.category>:nth-child(2)
{
    font-size: 1rem;
    
}


.category>:nth-child(3)
{
    font-size: 0.9rem;
    line-height: 25px;
}

.join-now{
    display: flex;
    align-items: center;
    gap: 2rem;
}

.join-now>img{
    width: 1rem;
}

.category:hover{
    background: var(--planCard);
    cursor: pointer;
}


@media screen and (max-width:768px)
{
    .programs-header{
        flex-direction: column;
        gap: 1rem;
        font-size: x-large;
        align-items: center;
        justify-content: center;
        margin-top: -4rem;
    
    }

    .category{
        width: 90%;
        margin-left: 1.3rem;
    }

    .programs-categories{
        flex-direction: column;
    }
}
